.wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  flex-direction: column;
  box-sizing: border-box;
  padding: 20px;
}
.introduction{
  position: absolute;
  top: 100px;
  display: flex;
  flex-direction: column;
}
.avatar{
  opacity: 70%;
  height: 200px;
  width: 200px;
  border: 2px solid white;
  border-radius: 10px;
}